*{
  box-sizing: border-box;
}

body{
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Inter', sans-serif;
  
  
}

.bottom-line{
  width: 478px;
  color: #757575;
  opacity: 0.2;
  border: 1;
  display: block;
}

.nav-bar{
  background-color: #8fb6ab;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}


.App{
  width: 550px;
  height: 720px;
  background-color: #65AA9A;
}

.globe-icon{
  font-size: 24px;
  color: white;
  margin-right: 7px
}

.nav-text{
  font-size: 15px;
  color: white;
}

/* F55A5A */

.marker{
  color: #F55A5A;
  font-size: 7px;
  margin-right: 5px;
}

.map-link{
  color: #eceaf1;
  font-size: 10.24px;
  margin-left: 7px;
}

.card-section{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 40px;
  margin-top: 40px;
  width: 478px;
}

.card-img{
  width: 125px;
  height: 168px;
  border-radius: 5px;
  /* padding-top: -10px; */
}

.card-text{
  /* padding-top: 40px; */
  margin-left: 17px;
}

.card-loc{
  font-size: 10.24px;
  text-transform: uppercase;
}



.card-title{
  font-size: 25px;
  margin-top: 5px;
  margin-bottom: 5px;
  color: whitesmoke;
}

.card-date{
  font-size: 10.24px;
}

.card-desc{
  font-size: 10.24px;
}